body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.center {
  width: 75%;
  margin: auto;
}
/* nav bar  */
.topnav {
  position: relative;
  overflow: hidden;
  background-color: #03045e;
  
}

.topnav ul li {
  float: left;
}


.topnav ul li a {
  display: block;
  color: #90e0ef;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav ul li a:hover {
  background-color: #023e8a;
  color: #90e0ef;
}

.topnav ul li .active {
  background-color: #0077b6;
  color: #90e0ef;
}



.topnav ul li.right {
  float: right;
}
.topnav ul li p {
  font-size: 17px;
  margin: 0;
  padding: 14px 16px;
  text-align: center;
  color: #90e0ef;
}
.topnav ul li img {
  display: block;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  padding: 7px;
}

.topnav ul li button {
  display: block;
  background-color: #0077b6;
  border: none;
  color: #90e0ef;
  padding: 7px 8px;
  margin: 7px 14px;
  font-size: 17px;
  border-radius: 10px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}
/* Responsive navigation menu (for mobile devices) */
@media screen and (max-width: 600px) {
  .topnav ul li {
    float: none;
    display: block;
  }
  
}

/* nav bar  */

/* controller */
.controller {
  display: flex;
  justify-content: space-between;
  margin: 20px auto;
}
.controller button {
  background-color: #90e0ef;
  border: #03045e solid 2px;
  color: #03045e;
  padding: 7px 15px;
  font-size: 17px;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}
.controller button.active {
  background-color: #03045e;
  border: #90e0ef solid 2px;
  color: #90e0ef;

}

/* controller */

/* question card */
.questionCard,
.userCard {
  display: flex;
  align-items: stretch;
  margin: 20px auto;
  border: #023e8a solid 2px;
  border-radius: 10px;
}
.questionCard .questionAvatar,
.userCard .userAvatar,
.userCard .user div.right {
  padding: 20px;
  display: flex;
  width: 33%;
  align-items: center;
  flex-direction: column;
  align-content: space-around;
  background-color: #03045e;
  color: #90e0ef;
  border-radius: 8px 0 0 8px;
  
}
.questionCard .questionAvatar img,
.userCard .userAvatar img {
  display: block;
  height: 120px;
  width: 120px;
  margin: auto 0;
  border-radius: 50%;
  border: #0077b6 solid 4px;
}
.questionCard .questionAvatar h3,
.userCard .userAvatar h3 {
  padding-top: 10px;
  text-align: center;
}
.questionCard .info,
.questionCard .form,
.questionCard .result,
.userCard .user {
  width: 77%;
  padding: 10px 10px 10px 20px;
  color: #03045e;
}
.questionCard .info h3,
.questionCard .form h3,
.questionCard .result h3,
.newQuestion h3 {
  padding: 10px 0;
  margin: 0 0 20px 0;
  border-bottom: #023e8a solid 3px;
}
.questionCard .info h5 {
  padding: 5px;
  border: #023e8a solid 2px;
  text-align: center;
  border-radius: 5px 20px;
  margin: 10px 0;
}
.questionCard .info h4 {
  text-align: center;
}

.questionCard .info a,
.questionCard .form button,
.newQuestion button,
.loginForm button {
  float: right;
  background-color: #0077b6;
  border: none;
  color: #90e0ef;
  padding: 14px 30px;
  margin: 10px 0;
  border-radius: 20px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}

/* question form */

.questionCard .form .inputContainer {
  margin-bottom: 20px;
  display: block;
  cursor: pointer;
  font-size: 22px;
  position: relative;
  padding-left: 35px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #03045e;
  font-size: 17px;
}

.questionCard .form .inputContainer input {
  position: absolute;
  cursor: pointer;
  opacity: 0;
}

.questionCard .form .inputContainer .mark {
  position: absolute;
  top: 0px;
  left: 0;
  background-color: #90e0ef;
  border-radius: 50%;
  height: 25px;
  width: 25px;
}

.questionCard .form .inputContainer:hover input ~ .mark {
  background-color: #90e0ef;
}

.questionCard .form .inputContainer input:checked ~ .mark {
  background-color: #03045e;
}

.questionCard .form .inputContainer .mark:after {
  content: "";
  position: absolute;
  display: none;
}

.questionCard .form .inputContainer input:checked ~ .mark:after {
  display: block;
}

.questionCard .form .inputContainer .mark:after {
  height: 8px;
	width: 8px;;
	border-radius: 50%;
	background: #90e0ef;
  top: 9px;
	left: 9px;
}

/* question form */

/* question result */
.questionCard .result h4,
.newQuestion h4 {
  text-align: center;
}
.questionCard .result div {
  padding: 15px 10px;
  border: #023e8a solid 2px;
  text-align: center;
  border-radius: 5px 20px;
  margin: 10px 0;
}

.questionCard .result div h5,
.questionCard .result div h4 {
  margin: 15px 0;
}
.questionCard .result div h4 {
  display: none;
}
.questionCard .result .progress {
  position: relative;
  padding: 0;
  margin: 10px auto;
  width: 70%;
  height: 22px;
  border: #023e8a solid 2px;
  border-radius: 10px;
  background-color: #90e0ef;
  text-align: center;
  overflow: hidden;
}

.questionCard .result .bar {
  position: absolute;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  height: 100%;
  color: #90e0ef;
  text-align: center;
  border: none;
  border-radius: 5px;
  background-color: #023e8a;
}

.questionCard .result div.active {
  color: #90e0ef;
  background-color: #0077b6;
}
.questionCard .result div.active h4 {
  display: block;
}

/* question result */

/* question card */

/* new question */
.newQuestion {
  display: flex;
  margin: 30px auto;
  padding: 10px;
  width: 250px;
  border: #023e8a solid 2px;
  color: #03045e;
  border-radius: 10px;
}

.newQuestion .center {
  width: 90%;
}

.newQuestion input {
  box-sizing: border-box;
  width: 100%;
  padding: 8px;
  margin: 17px 0;
  border: none;  
  border: 2px solid #03045e;
  border-radius: 10px; 
}

.newQuestion input:focus {
  outline: none;
}
.newQuestion button {
  padding: 10px 20px;
  margin-top: 20px;
}
/* new question */

/* user card */
.userCard .user {
  padding: 0;
  display: flex;
  justify-content: center;
  align-content: center;
}
.userCard .user div {
  text-align: center;
  padding: 10pxs;
}

.userCard .user div.left {
  width: 70%;
  align-self: center;
}
.userCard .user div.left h4 {
  padding: 20px 0 0;
}
.userCard .user div.left h2,
.userCard .user div.right h1 {
  padding: 10px 0 20px;
}

.userCard .user div.right {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 30%;
  border-radius: 0 8px 8px 0;
}
.userCard .user div.right h4 {
  padding: 0 0 10px 0;
  border-bottom: #0077b6 solid 5px;
}

/* user card */


/* login form */

.loginForm {
  text-align: center;
  border: #03045e solid 3px;
  background-color: #fff;
  color: #03045e;
  border-radius: 10px;
  margin: 30px auto;
  padding: 30px;
  display: flex;
  flex-direction: column;
  width: 70%;
}

.loginForm h1 {
  margin-bottom: 30px;
}

.loginForm h2 {
  margin: 10px 0;
}
.loginForm .selectContainer {
  width: 70%;
  position: relative;
  padding: 0;
  margin: 40px auto 0;
  
}

.loginForm .selectContainer select {
  font-size: 14px;
  padding: 7px 8px;
  border: 0;
  background-color: #03045e;
  color: #90e0ef;
  margin: 0;
  display: block;
  width: 100%;
  margin: auto;
  text-align: left;
}
.loginForm .selectContainer select:focus {
  outline: none;
}
.loginForm button {
  padding: 8px 15px;
  float: none;
  width: 40%;
  margin-top: 20px;
}

/* login form */

/* loading */
.loading {
  background-color: #0077b6;
  height: 5px;
}
.loadingText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(50%, 50%);
}
/* loading */
/* error */
.error {
  color: #03045e;
  margin-top: 20px;
}
.error h1 {
  text-align: center;
}
/* error */